<template>
    <div style="width:100%;overflow:hidden;position:relative;height:100vh;">
        <div class="loginNav">
            <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">资料修改</p>
        </div>
        <div style="overflow:auto;height:100vh;padding-top:0.84rem;box-sizing: border-box;">

            <div class="msgBox">
                <van-uploader class="calcWi msgLine" :after-read="onReadHead" accept="image/jpeg">
                    <div class="msgTitle f30">个人头像</div>
                    <img class="msgTo" src="../../assets/my/more.png" alt="">
                    <img class="msgHead" :src="$api.fileUrl + personalData.headImage" alt="">
                </van-uploader> 
            </div>
            <div class="msgBox">
                <div class="msgLine">
                    <div class="msgTitle f30">姓名</div>
                    <input type="text" class="f26 c999 msgName nameStyle" v-model="personalData.username">
                </div>
            </div>
            <!-- <div class="msgBox">
                <div class="msgLine">
                    <div class="msgTitle f30">身份证号码</div>
                        <div class="f26 c999 msgName">330681545454454454545</div>
                </div>
            </div> -->
            <div class="msgBox">
                <div class="msgLine">
                    <div class="msgTitle f30">管理班级</div>
                    <img class="msgTo" src="../../assets/my/more.png" alt="">
                        <div class="f26 c999 msgName">{{personalData.classesName}}</div>
                </div>
            </div>
            <div class="Introduction">
                <div class="msgTitle f30 IntroductionTitle">个人简介</div>
                <textarea class="content f26 descStyle"  cols="30" rows="10" v-model="personalData.teaDesc">
                </textarea>
            </div>
            <div class="photo">
                <div class="msgTitle f30 phoneTitle">教学风采 <p class="c999 ml-30">(最多上传10张)</p></div>
                <div class="phoneBox">
                    <div class="imgShow ml-22 mt-22" v-for="(item,index) in personalData.teaStyle" :key="index">
                        <img :src="$api.fileUrl + item" alt="">
                    </div>
                    <div class="imgShow ml-22 mt-22" v-if="personalData.teaStyle.length < 10">
                        <van-uploader :after-read="onRead" accept="image/jpeg">
                            <img src="../../assets/my/upload.png" alt="">
                        </van-uploader>
                    </div>
                </div>
            </div>
            <!-- <div style="height:2rem;"></div> -->
        </div>
        <div class="centerBtn f26 cfff text-center" @click="editSubmit">确认修改</div>
    </div>
    
</template>

<script>
export default {
  name: 'HelloWorld',
    data () {
        return {
            name: '杨会如',
            active: 2,
            personalData:{
                headImage:'',
                username:'',
                classesName:'',
                teaDesc:'',
                teaStyle:[]
            },
            icon: {
                home: require('../../assets/my/home.png'),
                hmeActive: require('../../assets/my/homeActive.png'),
                msg:require('../../assets/my/msg.png'),
                msgActive:require('../../assets/my/msgActive.png'),
                my:require('../../assets/my/my.png'),
                myActive:require('../../assets/my/myActive.png')
            },
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/my'});//返回上一层
        },
        onReadHead(file) {
            var form = new FormData();
            form.append('file',file.file);
            this.$fileHttp(this.$api.fileUpload,form).then(data => {
                if(data.code == 0){
                    this.personalData.headImage = data.data[0];
                }
            })
        },
        onRead(file) {
            var form = new FormData();
            form.append('file',file.file);
            this.$fileHttp(this.$api.fileUpload,form).then(data => {
                if(data.code == 0){
                    this.personalData.teaStyle.push(data.data[0]);
                }
            })
        },
        editSubmit(){
            this.$toast.loading({
                mask: true,
                duration:0
            });
            var param = {
                id : JSON.parse(localStorage.getItem('zhxy_user')).id,
                username : this.personalData.username,
                headImage : this.personalData.headImage,
                teaDesc : this.personalData.teaDesc,
                teaStyleUrl : this.personalData.teaStyle.toString()
            };
            this.$http({url:this.$api.my.saveOrUpdateTeacher},param).then(data => {
                console.log(data);
                if(data.code == 0){
                    var userMessage = JSON.parse(localStorage.getItem('zhxy_user'));
                    userMessage.username = this.personalData.username;
                    userMessage.headImage = this.personalData.headImage;
                    userMessage.teaDesc = this.personalData.teaDesc;
                    userMessage.teaStyle = this.personalData.teaStyle;
                    localStorage.setItem('zhxy_user',JSON.stringify(userMessage));
                    this.$toast('修改成功');
                }else{
                    this.$toast(data,msg);
                }
            },err => {
                this.$toast.clear();
            })
        }
    },
    created() {
        this.personalData = JSON.parse(localStorage.getItem('zhxy_user'));
    },
}
</script>

<style scoped>
    .loginNav{position: fixed;z-index: 999;}
    .msgBox{width: 100%;overflow: hidden;}
    .msgLine{margin-left: 0.3rem;border-bottom: 1px solid #f2f2f2;overflow: hidden;height: 1rem;line-height: 1rem;}
    .msgTitle{float: left;}
    .msgHead{width: 0.7rem;height: 0.7rem;display: block;float:right;border-radius: 50%;margin: 0.15rem 0.2rem 0 0;}
    .msgTo{width: 0.3rem;height: 0.3rem;float: right;margin: 0.35rem 0.3rem 0 0;}
    .msgName{float: right;margin-right: 0.3rem;}
    .Introduction{width: 100%;box-sizing: border-box;padding:0 0.3rem 0.3rem;border-bottom: 1px solid #f2f2f2;}
    .IntroductionTitle{line-height: 1rem;}
    .content{clear: both;}
    .photo{width: 100%;box-sizing: border-box;padding:0 0.3rem 2rem 0.3rem;}
    .phoneTitle{line-height: 1rem;}
    .phoneTitle p{display: inline-block;}
    .phoneBox{clear: both;overflow: hidden;}
    .imgShow{width: 1.2rem;height: 1.2rem;border-radius: 5px;overflow: hidden;float: left;}
    .imgShow img{width: 100%;height: 100%;display: block;}
    .imgShow:nth-child(5n+1){margin-left: 0;}
    .calcWi{width: calc(100% - 0.3rem)}
    .nameStyle{border:0;outline: none;text-align: right;width: 60%;}
    .descStyle{border: 0;outline: none;width: 100%;height: 100%;clear: both;}
    .centerBtn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;position: fixed;bottom: 0.6rem;left: 50%;margin-left: -3.45rem; }
</style>


